import {
  Component,
  EventEmitter,
  Input,
  Output,
  SimpleChanges,
  TemplateRef,
} from '@angular/core';
import { NgClass, NgTemplateOutlet } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import {
  actionDeleteSvg,
  actionEditSvg,
  actionViewSvg,
  expandIcon,
  sortSvg,
} from './assets';
import { animate, style, transition, trigger } from '@angular/animations';
import {
  CustomActionsDropdownComponent,
  IActions,
} from '../custom-actions-dropdown/custom-actions-dropdown.component';
import { CustomStatusLabelComponent } from "../custom-status-label/custom-status-label.component";

export interface ISmTableColumn {
  key: string;
  label: string;
  sort?: boolean;
  disabled?: boolean;
}

export interface ISmDynmaicTableConfig {
  columns: ISmTableColumn[];
  data: any[];
  dataStatus: 'LOADING' |'EMPTY' |'HAS_DATA' | 'No_Filter_Result';
}
@Component({
  selector: 'custom-sm-dynamic-table',
  imports: [
    TranslateModule,
    NgClass,
    NgTemplateOutlet,
    CustomActionsDropdownComponent,
    CustomStatusLabelComponent
],
  templateUrl: './custom-sm-dynamic-table.component.html',
  styleUrl: './custom-sm-dynamic-table.component.css',
})
export class CustomSmDynamicTableComponent {
  @Input() config: ISmDynmaicTableConfig = {} as ISmDynmaicTableConfig;
  @Input() actionsList: IActions[] = [];
  @Input() hasCheckBox: boolean = true;
  @Input() tableMaxHeight: string = '52vh';
  @Input() colTemplates: { [key: string]: TemplateRef<any> } = {};

 @Input() actionTemplate?: TemplateRef<{ $implicit: any }>;
   @Input() hasActionTemplate: boolean = false;
  @Output() sortColumn = new EventEmitter<
    string | number | symbol | undefined
  >();
  @Output() nameClick: EventEmitter<any> = new EventEmitter<any>();

  checkedSortIcon: SafeHtml;
  checkedActionViewSvg: SafeHtml;
  checkedActionEditSvg: SafeHtml;
  checkedActionDeleteSvg: SafeHtml;
  expandSvg: SafeHtml;
  private statusKey: string | null = null;
  constructor(private sanitizer: DomSanitizer) {
    const sortSvgIcon = sortSvg;
    this.checkedSortIcon = this.sanitizer.bypassSecurityTrustHtml(sortSvgIcon);
    const ActionView = actionViewSvg;
    this.checkedActionViewSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionView);
    const ActionEdit = actionEditSvg;
    this.checkedActionEditSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionEdit);
    const ActionDelete = actionDeleteSvg;
    this.checkedActionDeleteSvg =
      this.sanitizer.bypassSecurityTrustHtml(ActionDelete);

    const ExpandIcon = expandIcon;
    this.expandSvg = this.sanitizer.bypassSecurityTrustHtml(ExpandIcon);
  }

  onAction(row: any, handler: (row: any) => void): void {
    handler(row);
  }

  getNestedValue(obj: any, path: string): any {
    if (!path) return undefined;
    const parts = path.split(/[\.\[\]']+/).filter(Boolean);
    return parts.reduce((acc, key) => acc?.[key], obj);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['config']) {
      this.statusKey = this.findStatusKey();
    }
  }
  private findStatusKey(): string | null {
    const col = this.config?.columns?.find((c) =>
      (c.key ?? '').toLowerCase().includes('status')
    );
    return col?.key ?? null;
  }

  isRowInactive(row: any): boolean {
    if (!this.statusKey) return false;
    const val = this.getNestedValue(row, this.statusKey);
    return String(val).toLowerCase() === 'inactive';
  }
}
