import { Pipe, PipeTransform } from '@angular/core';
import { formatActivityTime } from '../utilities/activity-time.util';

@Pipe({
  name: 'activityTime'
})
export class ActivityTimePipe implements PipeTransform {

  transform(
    value: string | Date,
    options?: { activeNowSeconds?: number; locale?: string }
  ): string {
    return formatActivityTime(value, options);
  }

}
