const SECOND = 1_000;
const MINUTE = 60 * SECOND;
const HOUR   = 60 * MINUTE;
const DAY    = 24 * HOUR;

export function toDate(input: string | Date | null | undefined): Date | null {
  if (!input) return null;
  if (input instanceof Date) return isNaN(input.getTime()) ? null : input;
  const d = new Date(input);
  return isNaN(d.getTime()) ? null : new Date(d.getTime() - d.getTimezoneOffset() * 60000);
}

export function plural(n: number, unit: string): string {
  return n === 1 ? `${n} ${unit}` : `${n} ${unit}s`;
}

export function formatAbsolute(date: Date, locale = 'en'): string {
  const day = String(date.getDate()).padStart(2, '0');
  const mon = new Intl.DateTimeFormat(locale, { month: 'short' }).format(date);
  const year = date.getFullYear();
  return `${day} ${mon}, ${year}`;
}

/**
 * Main formatter
 */
export function formatActivityTime(
  input: string | Date,
  opts?: {
    now?: Date;               // for testing/overrides
    activeNowSeconds?: number;// threshold for "active now", default 60s
    locale?: string;          // month language
  }
): string {
  const date = toDate(input);
  if (!date) return '-';

  const now = opts?.now ?? new Date();
  const diffMs = now.getTime() - date.getTime();

  // Future timestamps: show absolute date
  if (diffMs < 0) return formatAbsolute(date, opts?.locale);

  const activeNowMs = (opts?.activeNowSeconds ?? 60) * SECOND;

  if (diffMs <= activeNowMs) return 'active now';
  if (diffMs < HOUR) {
    const mins = Math.floor(diffMs / MINUTE);
    return `${plural(mins, 'min')} ago`;
  }
  if (diffMs < DAY) {
    const hrs = Math.floor(diffMs / HOUR);
    return `${plural(hrs, 'hr')} ago`;
  }
  return formatAbsolute(date, opts?.locale);
}
