import { Component, Input } from '@angular/core';

type StatusKey =
  | 'paid'
  | 'not_paid'
  | 'active'
  | 'inactive'
  | 'open'
  | 'inprogress'
  | 'closed';

interface StyleDef {
  color: string;
  bg?: string;
  border?: string;
  label: string;
}

const STYLES: Record<StatusKey, StyleDef> = {
  paid: { color: '#12AB6C', bg: '#e4f2ec', border: '#12AB6C', label: 'PAID' },
  not_paid: {
    color: '#B42318',
    bg: '#f8e9e8',
    border: '#B42318',
    label: 'NOT PAID',
  },
  active: {
    color: '#12AB6C',
    bg: '#e4f2ec',
    border: '#12AB6C',
    label: 'Active',
  },
  inactive: {
    color: '#B42318',
    bg: '#f8e9e8',
    border: '#B42318',
    label: 'Inactive',
  },

  inprogress: {
    color: '#ffffff',
    bg: '#1F1F1F',
    border: '#1F1F1F',
    label: 'In Progress',
  },
  closed: {
    color: '#ffffff',
    bg: '#D9DBE1',
    border: '#D9DBE1',
    label: 'Closed',
  },
  open: { color: '#ffffff', bg: '#602650', border: '#602650', label: 'Open' },
};

@Component({
  selector: 'custom-status-label',
  imports: [],
  templateUrl: './custom-status-label.component.html',
  styleUrl: './custom-status-label.component.css',
})
export class CustomStatusLabelComponent {
  @Input({ required: true }) status!: string;

  @Input() uppercase = false;
  @Input() extraText :string = '';


  private normalize(val: string): StatusKey {
    const status = (val ?? '').trim().toLowerCase().replace(/\s+/g, '_');
    const map: Partial<Record<string, StatusKey>> = {
      paid: 'paid',
      not_paid: 'not_paid',
      not_payed: 'not_paid',
      active: 'active',
      inactive: 'inactive',
      open: 'open',
      inprogress: 'inprogress',
      'in-progress': 'inprogress',
      'in progress': 'inprogress',
      closed: 'closed',
    };
    return (map[status] ?? 'open') as StatusKey;
  }

  get key(): StatusKey {
    return this.normalize(this.status);
  }
  get s(): StyleDef {
    return STYLES[this.key];
  }
}
